import { mapGetters } from "vuex";
import { pay } from "@libs/wechat";
import { isWeixin } from "@utils";
import { rechargeWechat, getRechargeApi } from "@api/user";
import { add, sub } from "@utils/bc";
export default {
  name: "Recharge",
  props: {},
  data: function data() {
    return {
      navRecharge: ["账户充值", "佣金导入"],
      active: 0,
      payType: ["weixin"],
      from: isWeixin() ? "weixin" : "weixinh5",
      money: "",
      now_money: "",
      picList: [],
      activePic: 0,
      numberPic: "",
      rechar_id: 0,
      rechargeAttention: []
    };
  },
  computed: mapGetters(["userInfo"]),
  mounted: function mounted() {
    this.now_money = this.userInfo.now_money;
    this.getRecharge();
  },
  methods: {
    /**
     * 充值额度选择
     */
    getRecharge: function getRecharge() {
      var _this = this;

      getRechargeApi().then(function (res) {
        _this.picList = res.data.recharge_quota;

        if (_this.picList[0]) {
          _this.rechar_id = _this.picList[0].id;
          _this.numberPic = _this.picList[0].price;
        }

        _this.rechargeAttention = res.data.recharge_attention || [];
      }).catch(function (res) {
        _this.$dialog.toast({
          mes: res
        });
      });
    },

    /**
     * 选择金额
     */
    picCharge: function picCharge(idx, item) {
      this.activePic = idx;

      if (item === undefined) {
        this.rechar_id = 0;
        this.numberPic = "";
      } else {
        this.money = "";
        this.rechar_id = item.id;
        this.numberPic = item.price;
      }
    },
    navRecharges: function navRecharges(index) {
      this.active = index;
      this.rechar_id = this.picList[0].id;
      this.numberPic = this.picList[0].price;
      this.activePic = 0;
      this.money = "";
    },
    recharge: function recharge() {
      var _this2 = this;

      var that = this,
          price = Number(this.money);

      if (that.active) {
        if (price === 0) {
          return that.$dialog.toast({
            mes: "请输入您要转入的金额"
          });
        } else if (price < 0.01) {
          return that.$dialog.toast({
            mes: "转入金额不能低于0.01"
          });
        } else if (price > this.userInfo.brokerage_price) {
          return that.$dialog.toast({
            mes: "转入金额不能大于可提现佣金"
          });
        }

        this.$dialog.confirm({
          mes: "转入余额无法在转出，请确认转入",
          title: "转入余额",
          opts: [{
            txt: "确认",
            color: false,
            callback: function callback() {
              rechargeWechat({
                price: price,
                from: that.from,
                type: 1
              }).then(function (res) {
                that.now_money = add(price, parseInt(that.userInfo.now_money));
                that.userInfo.brokerage_price = sub(that.userInfo.brokerage_price, price);
                that.money = "";
                return that.$dialog.toast({
                  mes: res.msg
                });
              }).catch(function (res) {
                that.$dialog.toast({
                  mes: res.msg
                });
              });
            }
          }, {
            txt: "取消",
            color: false,
            callback: function callback() {
              return that.$dialog.toast({
                mes: "已取消"
              });
            }
          }]
        });
      } else {
        if (this.picList.length == this.activePic && price === 0) {
          return that.$dialog.toast({
            mes: "请输入您要充值的金额"
          });
        } else if (this.picList.length == this.activePic && price < 0.01) {
          return that.$dialog.toast({
            mes: "充值金额不能低于0.01"
          });
        }

        rechargeWechat({
          price: that.rechar_id == 0 ? that.money : that.numberPic,
          from: that.from,
          rechar_id: that.rechar_id
        }).then(function (res) {
          var data = res.data;

          if (data.type == "weixinh5") {
            location.replace(data.data.mweb_url);

            _this2.$dialog.confirm({
              mes: "充值余额",
              opts: [{
                txt: "已充值",
                color: false,
                callback: function callback() {
                  that.$router.replace({
                    path: "/user/account"
                  });
                }
              }, {
                txt: "查看余额",
                color: false,
                callback: function callback() {
                  that.$router.replace({
                    path: "/user/account"
                  });
                }
              }]
            });
          } else {
            pay(data.data).finally(function () {
              that.now_money = add(price, parseInt(that.userInfo.now_money));
              that.$dialog.toast({
                mes: "支付成功"
              });
            }).catch(function () {
              that.$dialog.toast({
                mes: "支付失败"
              });
            });
          }
        }).catch(function (res) {
          that.$dialog.toast({
            mes: res.msg
          });
        });
      }
    }
  }
};